<template>
  <div id="app">
    <header class="header">
      <div class="container header__wrap">
        <h1 class="header__logo">Авито</h1>
        <!-- <div class="header__favourite">
          <img src="./assets/img/heartFree.svg" alt />
        </div>-->
      </div>
    </header>

    <main>
      <section class="options-block">
        <b-container>
          <div class="options-block__header">Отфильтровать по:</div>
          <b-row class="options-block__options-wrap">
            <b-col lg="3" class="filter">
              <div class="filter__header">Категории:</div>
              <div class="filter__controls">
                <b-form-select
                  @change="changeCateg"
                  v-model="categ"
                  class="filter__select"
                  name="categoris"
                >
                  <option value>Все категории</option>
                  <option value="immovable">Недвижимость</option>
                  <option value="cameras">Камеры</option>
                  <option value="auto">Автомобили</option>
                  <option value="laptops">Ноутбуки</option>
                </b-form-select>
              </div>
            </b-col>
            <b-col lg="4" class="filter">
              <div class="filter__header">Цена:</div>
              <div class="filter__controls filter-price">
                <div class="filter-price__block">
                  <span>c</span>
                  <b-form-input
                    v-model.trim.number="beginPrice"
                    type="number"
                    class="filter-price__input"
                  ></b-form-input>
                </div>
                <div class="filter-price__block">
                  <span>по</span>
                  <b-form-input
                    v-model.trim.number="endPrice"
                    type="number"
                    class="filter-price__input"
                  ></b-form-input>
                </div>
              </div>
            </b-col>

            <b-col lg="5" class="filter">
              <div class="filter__header">Избранные:</div>
              <div class="filter__controls">
                <div>Показать только избранные:</div>

                <b-form-checkbox class="filter__chekbox" v-model="showFav"></b-form-checkbox>
                <!-- <b-form-checkbox-group v-model="showFav">
                  <b-form-checkbox value="that">Check that out</b-form-checkbox>
                </b-form-checkbox-group>-->
                <!-- <input v-model="showFav" type="checkbox" /> -->
              </div>
            </b-col>
          </b-row>
        </b-container>
      </section>

      <section class="options-block">
        <b-container class="container options-block__wrap">
          <div class="options-block__header">Отсортировать по:</div>
          <div class="options-block__options-wrap">
            <div class="filter">
              <div class="filter__controls">
                <b-form-select class="filter__select" v-model="typeSort">
                  <option value="rating">По популярности (рейтинг продавца)</option>
                  <option value="price">По цене</option>
                </b-form-select>
                <b-form-select v-model="directionSort" class="filter__select">
                  <option value="down">По убывания</option>
                  <option value="up">По возрастанию</option>
                </b-form-select>
              </div>
            </div>
          </div>
        </b-container>
      </section>

      <section v-if="loadSalers">
        <b-container class="container">
          <h1>{{productFiltered.length}}</h1>
        </b-container>
      </section>

      <section class="products">
        <div v-if="loadSalers" class="container products__wrapper">
          <Product
            v-for="product in productFiltered"
            :key="product.id"
            :product="product"
            :salers="findSalers(product.relationships.seller)"
          ></Product>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import Product from "./components/Product";

export default {
  components: {
    Product
  },
  data() {
    return {
      products: [],
      productsOrig: [],
      sellers: [],
      message: "Hello Vue!",
      categ: "",
      loadSalers: false,
      beginPrice: 0,
      endPrice: 0,
      showFav: false,
      typeSort: "price",
      directionSort: "down"
    };
  },
  mounted() {
    fetch("https://avito.dump.academy/products")
      .then(data => data.json())
      .then(data => {
        this.products = data.data;
        this.productsOrig = data.data;
        // this.productFiltered = data.data;
        this.beginPrice = this.getMinPrice(this.products);
        this.endPrice = this.getMaxPrice(this.products);
      });

    fetch("https://avito.dump.academy/sellers")
      .then(data => data.json())
      .then(data => {
        this.sellers = data.data;
        this.loadSalers = true;
      });
  },
  methods: {
    getMaxPrice(mass) {
      const nirmalizeMass = mass.filter(el => el.price);
      let max = 0;

      nirmalizeMass.forEach(el => {
        if (el["price"] > max) {
          max = el["price"];
        }
      });
      return max;
    },
    getMinPrice(mass) {
      const nirmalizeMass = mass.filter(el => el.price);
      return nirmalizeMass.reduce((min, p) => {
        return p.price < min ? p.price : min;
      }, nirmalizeMass[0].price);
    },
    changeCateg() {
      //хард ресет цен в инпутах
      //передаём массив отфильтрованный ТОЛЬКО по категориии
      const xex = this.products.filter(el => {
        return !this.categ || el.category == this.categ;
      });
      this.beginPrice = this.getMinPrice(xex);
      this.endPrice = this.getMaxPrice(xex);
    },
    findSalers(id) {
      return this.sellers.find(el => {
        return el.id == id;
      });
    }
  },
  computed: {
    productFiltered: {
      get: function() {
        return (
          this.products
            // фильтр по категории
            .filter(el => {
              return !this.categ || el.category == this.categ;
            })
            // фильтр по ценам
            .filter(product => {
              if (!product.price) return true;
              return (
                Number(product.price) >= this.beginPrice &&
                Number(product.price) <= this.endPrice
              );
            })
            //сотрировка
            .sort((lastOne, nextOne) => {
              //по рейтингу
              if (this.typeSort == "rating") {
                const ratePrev = this.findSalers(lastOne.relationships.seller)
                  .rating;
                const reteNext = this.findSalers(nextOne.relationships.seller)
                  .rating;
                if (this.directionSort == "up") {
                  return ratePrev > reteNext ? 1 : -1;
                } else {
                  return ratePrev < reteNext ? 1 : -1;
                }
                //по цене
              } else if (this.typeSort == "price") {
                const pricePrev = lastOne.price;
                const priceNext = nextOne.price;
                if (this.directionSort == "up") {
                  return pricePrev > priceNext ? 1 : -1;
                } else {
                  return pricePrev < priceNext ? 1 : -1;
                }
              }
            })
        );
      }
    },
    set: function(newValue) {
      this.products = newValue;
    }
  },
  watch: {
    showFav: function(newV, oldV) {
      if (this.showFav) {
        const massIndexFav = JSON.parse(localStorage.fav);
        const massFav = [];
        massIndexFav.forEach(indexFav => {
          const favItem = this.productsOrig.find(el => el.id == indexFav);
          massFav.push(favItem);
        });
        this.products = massFav;
      } else {
        this.products = this.productsOrig;
      }
    }
  }
};
</script>


